var faker = require("faker");

var hash = require("object-hash");

var animalsData = require("./animals");

var random = function random(min, max) {
  return Math.floor(Math.random() * (max - min) + min);
};

var AGES = ["Baby", "Young", "Adult", "Senior"];
var GENDERS = ["Male", "Female", "Unknown"];
var SIZES = ["Small", "Medium", "Large", null];
var COATS = ["Short", "Medium", "Long", null];
var ATTRIBUTES = [true, false, null];
var IMAGE_PROVIDERS = ["http://placecorgi.com/", "http://placekitten.com/", "http://placebear.com/"];
var STATUSES = ["adoptable", "adopted"];

var genOrg = function genOrg() {
  var state = faker.address.stateAbbr();
  var org = state + faker.random.number({
    min: 100,
    max: 999
  });
  return {
    org: org,
    state: state
  };
};

var photo = function photo() {
  var img = IMAGE_PROVIDERS[faker.random.number({
    min: 0,
    max: IMAGE_PROVIDERS.length - 1
  })];
  return {
    small: img + "100/100",
    medium: img + "300/300",
    large: img + "600/600",
    full: img + "800/800"
  };
};

var animal = function animal() {
  var id = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : random(1000000, 9999999);
  faker.seed(id);

  var _genOrg = genOrg(),
      org = _genOrg.org,
      state = _genOrg.state;

  var type = animalsData[id % animalsData.length];
  return {
    id: id,
    name: faker.name.firstName(),
    type: type,
    species: type,
    url: "https://frontendmasters.com/?ref=pet",
    organization_id: org,
    breeds: {
      primary: faker.random.word(),
      secondary: faker.random.boolean() ? faker.random.word() : null,
      mixed: faker.random.boolean(),
      unknown: faker.random.boolean()
    },
    colors: {
      primary: faker.commerce.color(),
      secondary: faker.commerce.color(),
      tertiary: faker.random.boolean() ? faker.commerce.color() : null
    },
    age: AGES[faker.random.number({
      min: 0,
      max: AGES.length - 1
    })],
    gender: GENDERS[faker.random.number({
      min: 0,
      max: GENDERS.length - 1
    })],
    size: SIZES[faker.random.number({
      min: 0,
      max: SIZES.length - 1
    })],
    coat: COATS[faker.random.number({
      min: 0,
      max: COATS.length - 1
    })],
    attributes: {
      spayed_neutered: ATTRIBUTES[faker.random.number({
        min: 0,
        max: ATTRIBUTES.length - 1
      })],
      house_trained: ATTRIBUTES[faker.random.number({
        min: 0,
        max: ATTRIBUTES.length - 1
      })],
      declawed: ATTRIBUTES[faker.random.number({
        min: 0,
        max: ATTRIBUTES.length - 1
      })],
      special_needs: ATTRIBUTES[faker.random.number({
        min: 0,
        max: ATTRIBUTES.length - 1
      })],
      shots_current: ATTRIBUTES[faker.random.number({
        min: 0,
        max: ATTRIBUTES.length - 1
      })]
    },
    environment: {
      children: ATTRIBUTES[faker.random.number({
        min: 0,
        max: ATTRIBUTES.length - 1
      })],
      dogs: ATTRIBUTES[faker.random.number({
        min: 0,
        max: ATTRIBUTES.length - 1
      })],
      cats: ATTRIBUTES[faker.random.number({
        min: 0,
        max: ATTRIBUTES.length - 1
      })]
    },
    tags: [],
    description: faker.lorem.text(10),
    photos: Array.from({
      length: faker.random.number({
        min: 1,
        max: 5
      })
    }).map(photo),
    status: STATUSES[faker.random.number({
      min: 0,
      max: STATUSES.length - 1
    })],
    published_at: new Date(0).toISOString(),
    contact: {
      email: faker.internet.email(),
      phone: faker.phone.phoneNumber(),
      address: genAddress(state),
      _links: {
        self: {
          href: "/v2/animals/" + id
        },
        organization: {
          href: "/v2/organizations/" + org.toLowerCase()
        },
        type: {
          href: "/v2/types/" + type
        }
      }
    }
  };
};

var genAddress = function genAddress(state) {
  return {
    address1: faker.address.streetAddress(),
    address2: faker.address.secondaryAddress(),
    city: faker.address.city(),
    state: state,
    postcode: faker.address.zipCode(),
    country: "US"
  };
};

module.exports.animal = animal;

var animals = function animals(options) {
  var seed = hash.sha1(options);
  faker.seed(parseInt(seed, 16) % 9999999);
  var pets = Array.from({
    length: 20
  }, function () {
    return faker.random.number({
      min: 1000000,
      max: 9999999
    });
  }).map(animal);
  return pets;
};

module.exports.animals = animals;

var breeds = function breeds(type) {
  var seed = hash.sha1(type);
  faker.seed(parseInt(seed, 16) % 9999999);
  var breedsRes = Array.from({
    length: 20
  }, function () {
    var type = faker.random.word();
    return {
      name: type,
      _links: {
        type: {
          href: "/v2/type/" + type
        }
      }
    };
  });
  return breedsRes;
};

module.exports.breeds = breeds;

var organization = function organization(id) {
  var seed = hash.sha1(id.toUpperCase());
  faker.seed(parseInt(seed, 16) % 9999999);
  var state = id.slice(0, 2).toUpperCase();
  return {
    id: id.toUpperCase(),
    name: faker.company.companyName(),
    email: faker.internet.email(),
    phone: faker.phone.phoneNumber(),
    address: genAddress(state),
    hours: {
      monday: null,
      tuesday: null,
      wednesday: null,
      thursday: null,
      friday: null,
      saturday: null,
      sunday: null
    },
    url: "https://frontendmasters.com/?ref=pet",
    website: faker.internet.url(),
    mission_state: faker.company.catchPhrase(),
    adoption: {
      policy: null,
      url: null
    },
    social_media: {
      facebook: "https://www.facebook.com/FrontendMasters/",
      twitter: "https://twitter.com/FrontendMasters/",
      youtube: "https://www.youtube.com/channel/UCzumJvwc0KBrdq4jpvOR7RA",
      instagram: "https://www.instagram.com/frontendmasters/",
      pinterest: null
    },
    photos: Array.from({
      length: faker.random.number({
        min: 1,
        max: 5
      })
    }).map(photo),
    _links: {
      self: {
        href: "/v2/organizations/".concat(id.toLowerCase())
      },
      animals: {
        href: "/v2/animals?organization=".concat(id.toLowerCase())
      }
    }
  };
};

module.exports.organization = organization;

var organizations = function organizations(options) {
  var seed = hash.sha1(options);
  faker.seed(parseInt(seed, 16) % 9999999);
  var orgs = Array.from({
    length: 20
  }, function () {
    return genOrg().org;
  }).map(organization);
  return orgs;
};

module.exports.organizations = organizations;

var typesRes = require("./mock-types");

module.exports.types = function () {
  return typesRes;
};

function type(slug) {
  var res = typesRes.filter(function (item) {
    return item.name.toLowerCase().replace(/&\s/g, "").replace(/\s/g, "-").replace(/,/g, "") === slug;
  });

  if (res.length) {
    return res[0];
  } else {
    return typesRes[0];
  }
}

module.exports.type = type;